import React from 'react';
import { makeStyles } from '@material-ui/styles';
import * as Config from '../config';

import Divider from '../components/Divider';
import ProjectCard from '../components/ProjectCard';

const useStyles = makeStyles({
	root: {
		backgroundColor: 'white',
		color: 'black',
		width: '100%',
		height: 'fit-content',
		padding: '2rem'
	},
	body: {
		position: 'relative',
	},
});

export default function Projects(props) {
	const classes = useStyles();
	const [projectSearch, setProjectSearch] = React.useState("");

	const handlePlayerSearch = (e) => {
		setProjectSearch(e.target.value);
	}

	const handleSearchList = () => {
		let result = Config.projectList.filter((project) => {
			if (!project.isVisible) return false;
			const searchInput = projectSearch.toLowerCase();
			const isFound = (val) => {
				if (Array.isArray(val)) {
					return val.some(a => a.toLowerCase().search(searchInput) !== -1);
				}
				return String(val).toLowerCase().search(searchInput) !== -1;
			}
			return isFound(project.title)
				|| isFound(project.platform)
				|| isFound(project.engine)
				|| isFound(project.language)
				|| isFound(project.year)
				|| isFound(project.tags)
				;
		});
		return result.map((project, index) => (
			<ProjectCard
				key={`projectCard-${index}`}
				project={project}
				showImageModal={props.showImageModal}
				hideImageModal={props.hideImageModal}
			/>
		));
	}

	return (
		<div id="projects" className={classes.root}>
			<h1>Projects</h1>
			<Divider color="black" width="100%" height="2px" />
			{projectSearch !== "" ? <p>Found <strong>{handleSearchList().length}</strong> projects</p> : <p>There is a total of <strong>{handleSearchList().length}</strong> projects</p>}
			<br />
			<div className="project-search-input">
				<input
					type="text"
					placeholder="Search for project (title/platform/engine/language/year/tag)"
					onChange={(e) => handlePlayerSearch(e)}
				/>
			</div>
			<div className={classes.body}>
				{projectSearch === "" ? (
					Config.projectList.map((project, index) => (
						<ProjectCard
							key={`projectCard-${index}`}
							project={project}
							showImageModal={props.showImageModal}
							hideImageModal={props.hideImageModal}
						/>
					))
				) : (
					<div>{handleSearchList()}</div>
				)}
			</div>
		</div>
	)
}


