import React from 'react';
import { makeStyles, withStyles } from '@material-ui/styles';

import Divider from './Divider';
import Chip from '@material-ui/core/Chip';

const useStyles = makeStyles({
  root: {
    height: "fit-content",
    marginBottom: "20px",
    flexDirection: "column",
    backgroundColor: "white",
  },
  header: {
    position: "relative",
    backgroundColor: "black",
    display: "flex",
    justifyContent: "flex-end",
    alignItems: "center",
  },
  body: {
    position: "relative",
    padding: "20px",
    color: "black",
    border: "2px solid black",
  },
  title: {
    padding: "20px",
    marginRight: "auto",
    marginLeft: "0px",
    color: "white",
  },
  btn: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    bottom: 0,
    backgroundColor: 'transparent',
    border: '2px solid white',
    color: 'white',
    fontWeight: 'bold',
    textTransform: 'uppercase',
    width: '150px',
    marginLeft: "10px",
    padding: "0.5rem",
    transition: '250ms ease-in-out',
    '&:hover': {
      backgroundColor: 'white',
      color: 'black',
    },
  },
  btnContainer: {
    marginLeft: "auto",
    display: "flex",
    flexDirection: "row",
    marginRight: "20px",
    cursor: "default",
  },
  scrollContainer: {
    height: "400px",
    display: "flex",
    flexDirection: "row",
    overflow: "auto hidden",
    marginTop: 10,
  },
  scrollContainerChild: {
    paddingRight: 10,
    paddingBottom: 10,
    transition: '150ms ease-in-out',
    '&:last-child': {
      paddingRight: 0
    },
		'&:hover': {
			cursor: 'pointer'
		}
  },
});

const TagChip = withStyles({
  root: {
    borderColor: "black",
    color: "black",
    marginRight: 5,
    '&:last-child': {
      marginRight: 0
    },
    borderRadius: 0,
  },
})(Chip);

export default function ProjectCard(props) {
  const classes = useStyles();
  return (
    <div
      className={classes.root}
      style={{
        display: props.project.isVisible ? "flex" : "none",
      }}
    >
      <div className={classes.header}>
        <h2 className={classes.title}>{props.project.title}</h2>
        <div className={classes.btnContainer}>
          {props.project.visitLink && (
            <form action={props.project.visitLink} target="_blank" rel="noopener noreferrer">
              <button className={classes.btn} type="submit">Visit</button>
            </form>
          )}
        </div>
      </div>
      <div className={classes.body}>
        <h3>Platform: {props.project.platform}</h3>
        <h3>Engine: {props.project.engine}</h3>
        <h3>Language: {props.project.language}</h3>
        <h3>Year: {props.project.year}</h3>
        {props.project.description && (
          <div>
            <Divider color="black" width="100%" height="2px" />
            <h3>Description</h3>
            <p>{props.project.description}</p>
          </div>
        )}
        {props.project.screenshots && props.project.screenshots.length > 0 && (
          <div>
            <Divider color="black" width="100%" height="2px" />
            <h3>{props.project.screenshots.length > 1 ? "Screenshots" : "Screenshot"}</h3>
            <div className={classes.scrollContainer}>
              {props.project.screenshots.map((img, index) => (
                <img
                  key={`projectScreenshot-${index}`}
                  className={classes.scrollContainerChild}
                  src={img}
                  width="auto"
                  height="auto"
                  alt=""
                  onClick={() => {
                    props.showImageModal(img);
                  }}
                />
              ))}
            </div>
          </div>
        )}
        {props.project.videos && props.project.videos.length > 0 && (
          <div>
            <Divider color="black" width="100%" height="2px" />
            <h3>{props.project.videos.length > 1 ? "Videos" : "Video"}</h3>
            <div className={classes.scrollContainer}>
              {props.project.videos.map((video, index) => (
                <video key={`projectVideo-${index}`} className={classes.scrollContainerChild} width="auto" height="auto" controls>
                  <source src={video} type="video/mp4" />
                </video>
              ))}
            </div>
          </div>
        )}
        {props.project.itchIoEmbed && props.project.itchIoEmbed.link && props.project.itchIoEmbed.id && props.project.itchIoEmbed.label && (
          <div>
            <Divider color="black" width="100%" height="2px" />
            <h3>Download</h3>
            <iframe
              frameborder="0"
              src={`https://itch.io/embed/${props.project.itchIoEmbed.id}?linkback=true&amp;bg_color=ffffff&amp;fg_color=000000&amp;link_color=000000&amp;border_color=000000`}
              width="552"
              height="167"
              style={{
                marginTop: 10
              }}
            >
              <a
                href={props.project.itchIoEmbed.link}
              >
                {props.project.itchIoEmbed.label}
              </a>
            </iframe>
          </div>
        )}
        {props.project.tags && props.project.tags.length > 0 && (
          <div>
            <Divider color="black" width="100%" height="2px" />
            <h3>Tags</h3>
            <div style={{
              display: 'flex',
              flexWrap: 'wrap',
              listStyle: 'none',
              marginTop: 10,
            }}>
              {props.project.tags.map((tag, index) => (
                <TagChip key={`tagChip-${index}`} label={tag} variant="outlined" />
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  )
}


